import React, { useState, useEffect } from "react";
import { Link } from "react-scroll";
import downloadImage from "../assets/svg/download.svg";
import hamburgerMenu from "../assets/svg/menu.svg";
export default function Navbar() {
  const [state, setstate] = useState({ menu: true });
  useEffect(() => {
    window.addEventListener("resize", function () {
      if (window.innerWidth > 600 && !state.menu) {
        setstate({ menu: true });
      }
    });
    return () => {
      window.removeEventListener("remove", function () {});
    };
  }, [window.innerWidth]);
  console.log(state);
  return (
    <div className="Navbar">
      <div className="navbar__left">
        <div className="logo">shakirck</div>
        <img
          className="hamburger-menu"
          onClick={() => setstate({ menu: !state.menu })}
          src={hamburgerMenu}
        />
      </div>
      <div className={state.menu ? "navbar__right" : "navbar__right hide"}>
        <Link
          activeClass="active"
          to="Home"
          spy={true}
          smooth={true}
          duration={1000}
        >
          Home
        </Link>

        <Link
          activeClass="active"
          to="About"
          spy={true}
          smooth={true}
          duration={1000}
        >
          About
        </Link>

        <Link
          activeClass="active"
          to="Portfolio"
          spy={true}
          smooth={true}
          duration={1000}
        >
          Portfolio
        </Link>

        <Link
          activeClass="active"
          to="Contact"
          spy={true}
          smooth={true}
          duration={1000}
        >
          Contact
        </Link>
        <a
          href="https://drive.google.com/file/d/1S0pVWLaSd_UHsEWi8vvPo8HSCvvZ6r17/edit"
          target="_blank"
          style={{
            // backgroundColor: "#0fc684",
            // padding: "6px",
            // borderRadius: "5px",
            fontWeight: 500,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img
            className="download-resume-img"
            src={downloadImage}
            style={{ padding: "1px", marginRight: "2px", color: "white" }}
          />
          Resume
        </a>
      </div>
    </div>
  );
}
